var qs = require('qs');
import axios from "axios";
import {store} from "@/store/store";
import PayplugHeader from "../classes/PayplugHeader";
import PayplugLogin from "../classes/PayplugLogin";
import PayplugSubscribe from "../classes/PayplugSubscribe";
import PayplugLogged from "../classes/PayplugLogged";

export async function $post(url, data) {
	// eslint-disable-next-line no-useless-catch
	try {
		return axios.post(url,  data );

	} catch (error) {
		throw error;
	}
}

export function $init(){

	$post(
		store.settings.ajax_url,
		'action=payplug_init'

	).then( (response) => {
		store.settings.logged = response.data.data.settings.logged;
		store.settings.mode = response.data.data.settings.mode;

		populate(response.data.data);

		store.rawData = response.data.data;
	})
	.catch(error => {
		//commit('setError', error);
		console.log(error);
	})
	.finally(function(){
		//commit('setLoading', false);
		store.settings.loading.global = false;

	})
}

export function $login() {
	//TODO:: missing NONCE

	let data = {
		"action": "payplug_login",
		"payplug_email": store.data.payplug_email,
		"payplug_password": store.data.payplug_password
	};

	store.error.login = null;

	$post(
		store.settings.ajax_url,
		qs.stringify(data)

	).then( (response) => {
		store.settings.logged = response.data.data.settings.logged;
		store.settings.mode = response.data.data.settings.mode;
		populate(response.data.data);
		store.rawData = response.data.data;

	}).catch(function (error) {
		const { response } = error;
		const { ...errorObject } = response; // take everything but 'request'
		store.error.login = errorObject.data.data.message;

	}).finally(function(){
		store.settings.loading.login = false;
	})

}

export function $logout(){

	let data = {"action": "payplug_logout",};
	$post(
		store.settings.ajax_url,
		qs.stringify(data)

	).then( (response) => {
		console.log(response);
		store.settings.logged = false;

	}).catch(function (error) {
		console.log(error);

	}).finally(function(){
		store.settings.loading.logout = false;
	})

}


export function populate(data){
	store.translations.header = new PayplugHeader(data.header);
	store.translations.login = new PayplugLogin(data.login);
	store.translations.subscribe = new PayplugSubscribe(data.subscribe);
	store.translations.logged = new PayplugLogged(data.logged, data.payplug_wooc_settings);
}
